.App {
  text-align: center;
}

/* css */
html {
  scroll-behavior: smooth;
}

.create {
  max-width: 800px;
  width: 80%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.create img {
  width: 50px;
  margin: 50px auto;
  display: block;
}

.create label {
  text-transform: uppercase;
  display: block;
  margin: 10px 0;
}

#blue_team_name {
  background: #0a4264;
}

#red_team_name {
  background: #be2038;
}

#watch_team_name {
  background: #146356;
}

.create .confirm {
  position: static;
  margin: 0 auto;
  display: block;
  transition: all.3s;
}

.confirm {
  background: #fff;
  color: #000;
  border: 0;
  outline: 0;
  text-align: center;
  line-height: 30px;
  font-weight: 800;
  font-size: 21px;
  margin: 0;
  padding: 0 20px;
  cursor: pointer;
}

.confirm[disabled] {
  background: #515151;
}

.create input,
.create select {
  width: 100%;
  height: 50px;
  line-height: 50px;
  color: #fff;
  display: block;
  border: 0;
  outline: none;
  padding: 0 20px;
  font-size: 21px;
  background: #000;
  margin: 0 0 40px;
}

* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

ul,
li {
  list-style: none;
}

a {
  color: inherit;
}

p,
label {
  margin: 0px;
  color: white;
}

h1,
span {
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.login-content {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.chat-box {
  background-color: black;
  width: 1070px;
  height: 350px;
  margin-bottom: 30px;
  border-radius: 3px;
  box-sizing: border-box;
  align-items: flex-end;
}

.chat-wrap {
  height: 280px;
  overflow: scroll;
  overflow-x: hidden;
}

.chat {
  display: flex;
  align-items: center;
  padding: 12px;
  margin-left: 12px;
}

.my-chat {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 12px;
  margin-right: 12px;
  color: cornflowerblue;
}

.send-btn {
  margin-right: 15px;
  background-color: blanchedalmond;
  outline: none;
  border: none;
  padding: 3px;
  border-radius: 3px;
  width: 10%;
  height: 30px;
  cursor: pointer;
}

.chat-box input {
  outline: none;
  border: none;
  background: black;
  color: white;
  /* border-bottom: white; */
  height: 30px;
  width: 80%;
  border: 1px solid blanchedalmond;
  border-radius: 3px;
  padding-left: 15px;
  margin-left: 20px;
  display: flex;
}

main {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}
.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.line-wrap {
  width: 48px;
  height: 48px;
  margin-right: 20px;
  position: relative;
}

.App-link {
  color: #61dafb;
}

.active-line::after {
  content: "";
  border-bottom: 2px solid darkgoldenrod;
  width: 48px;
  /* height: 2px; */
  /* height: 100px; */
  position: absolute;
  left: 0px;
}

.champion-box {
  background-color: black;
  padding: 24px;
  margin-bottom: 24px;
  border-radius: 3px;
  min-width: 1024px;
  width: 1024px;
  min-height: 1024px;
}

.competion {
  min-width: 1024px;
  width: 1024px;
  background-color: black;
  margin-bottom: 30px;
  margin-top: 30px;
}

.champion {
  display: inline-block;
  font-size: 11px;
  width: 75px;
  height: 75px;
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
}

.search-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 25px;
  padding-left: 12px;
}

.search-box input {
  outline: none;
  border: none;
  background: black;
  color: white;
  /* border-bottom: white; */
  height: 30px;
  border: 1px solid blanchedalmond;
  border-radius: 3px;
  padding-left: 15px;
}

.champion-icon {
  width: 48px;
  height: 48px;
  transition: all 0.3s;
}
.line-icon {
  width: 48px;
  height: 48px;
  margin-right: 20px;
  cursor: pointer;
}

#blue-second {
  position: absolute;
  left: 133px;
}
#red-second {
  position: absolute;
  left: 39px;
}

.line-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.pick {
  width: 20%;
  display: inline-block;
  text-align: center;
  position: relative;
  padding: 2px;
}

.team-picks {
  background: #000;
  border-radius: 4px;
  margin-bottom: 40px;
  padding: 4px 4px;
  width: 50%;
}

.pick-image {
  width: 100%;
  padding-top: 181%;
  background: #212121;
  position: relative;
  margin-top: 2px;
  margin-bottom: -4px;
  transition: background-color 0.35s ease-out;
}

.team-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 50%;
}

.team-img {
  font-weight: 800;
  background: url(data:image/png;base64,)
    no-repeat 100% 0 #000;
  margin: 0;
  line-height: 79px;
  padding: 0 180px 0 10px;
  text-transform: uppercase;
  font-size: 24px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  width: 100px;
  height: 78px;
  position: relative;
}

.red-team {
  background: url(data:image/png;base64,)
    no-repeat 0 0 #000;
}

.ben-pick-image {
  width: 100%;
  padding-top: 181%;
  background: #212121;
  position: relative;
  margin-top: 2px;
  margin-bottom: -4px;
  transition: background-color 0.35s ease-out;
}
.ben-pick-image .active {
  height: 14px;
  display: inline-block;
  position: absolute;
  bottom: 0;
  width: 100%;
  left: 0;
  -webkit-animation: active-grow 1 0.35s ease-out;
  animation: active-grow 1 0.35s ease-out;
}
.ben-pick-image:after {
  content: " ";
  display: block;
  width: 56px;
  height: 56px;
  background: url(data:image/png;base64,)
    no-repeat;
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translate(-50%);
  -ms-transform: translate(-50%);
  transform: translate(-50%);
}

.pick-card-wrap {
  display: flex;
}

.pick-card-wrap > div:first-child {
  margin-right: 20px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.pick img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transition: opacity 0.35s ease-out;
}

.select-btn {
  width: 100px;
  height: 35px;
  background: none;
  border: 1px solid blanchedalmond;
  border-radius: 3px;
  color: white;
  cursor: pointer;
}

.link_box {
  display: flex;
  align-items: center;
  cursor: pointer;
  width: 170px;
}

.copy_icon {
  margin-left: 10px;
}
